import React, { useEffect, useState } from "react";
import { Link } from "gatsby";
import { Card, Col, Image, Row } from "react-bootstrap";
import { StaticImage, GatsbyImage , getImage } from "gatsby-plugin-image";
import Img from "gatsby";

const BlogsData = (data) => {
  const { data: propsData, blogImages } = data;

  const [blogsData, setBlogsData] = useState([]);
  console.log("blogsData::", blogsData);

  const loadBlogs = () => {
    const blogsArr = [];
    propsData.edges.sort((a, b) => b.node.keyId - a.node.keyId);
    if (propsData?.edges?.length > 0) {
      for (let blog of propsData?.edges) {
        for (let blogImgObj of blogImages) {
          const jsonBlogImgEndPath = blog?.node?.image?.split("/");
          const graphBlogImgEndPath =
            blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src?.split(
              "/"
            );
          if (jsonBlogImgEndPath?.[2] === graphBlogImgEndPath?.[4]) {
            blog.node.blogImg = blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src
            blogsArr.push({blog
            })
            setBlogsData(blogsArr);
          }
        }
      }
    }
  };

  useEffect(() => {
    loadBlogs();
  }, []);
  // blogsData.sort((a, b) => b.blog?.node.keyId - a.blog?.node.keyId);

  // console.log("propsData::", propsData);
  // console.log("blogImages::", blogImages);

  return (
    <section className="section-develop" id="ourteam">
      <div className="container grid"></div>
      <div className="container">
        <div className="row pb-5 mb-5">
          <Row>
            {/* {blogsData?.length > 0 &&
              blogsData?.map((imgEle) => {
                const { blog } = imgEle;
                const img = getImage(blog?.node?.gatsbyImageData?.images?.fallback?.src)
              console.log("img::",img)
                return (
                  <Col md={6}>
                    <StaticImage
                       src={`https://justtrytech.com/${blog?.node?.gatsbyImageData?.images?.fallback?.src}`}
                      alt="Unraveling Defi explained in image"
                      className="w-50 mb-5 shadow"
                    />
                  </Col>
                );
              })} */}
          </Row>
          {blogsData?.map((ele, ind) => {
            const { blog } = ele;

            return (
              <div className="col-md-4 col-sm-6 ">
                <Card
                  key={ele.id}
                  className="developer card border rounded shadow mb-4"
                >
                  <Link to={`${blog?.node.slug}`} className="text-decoration-none">
                    {/* once image add in src/images/img-src after add to src. for time being and check the below the condition */}

                    {/* {node?.slug ===
                      "exclusive-complete-guide-decentralized-finance" && (
                      <StaticImage
                        src={"../../images/blog-1.png"}
                        alt="Unraveling Defi explained in image"
                      />
                    )}
                    {node?.slug === "best-ai-crypto-trading-bots" && (
                      <StaticImage
                        src={"../../images/blog-2.png"}
                        alt="AI Crypto Trading Bot explained in image"
                      />
                    )}
                    {node?.slug === "blockchain-supply-chain" && (
                      <StaticImage
                        src={"../../images/blog-3.png"}
                        alt="Blockchain in supply chaing explained in image"
                      />
                    )}
                    {node?.slug === "ecommerce-automotive-industry" && (
                      <StaticImage
                        src={"../../images/blog-4.png"}
                        alt="E-commerce in automotive explained in image"
                      />
                    )}
                    {node?.slug === "blockchain-in-pharma-industry" && (
                      <StaticImage
                        src={"../../images/blog-5.png"}
                        alt="Pharma in blockchain development explained in image"
                      />
                    )}
                    {node?.slug ===
                      "how-to-build-software-product-guide-for-beginners" && (
                      <StaticImage
                        src={"../../images/blog-6.png"}
                        alt="Software products development explained in image"
                      />
                    )}
                    {node?.slug === "free-crypto-trading-bots-2024" && (
                      <StaticImage
                        src={"../../images/blog-7.png"}
                        alt="Crypto trading free bots explained in image"
                      />
                    )}
                    {node?.slug === "web3-arena-graph-revolution" && (
                      <StaticImage
                        src={"../../images/blog-8.png"}
                        alt="Web3 arena graphs explained in image"
                      />
                    )}
                    {node?.slug ===
                      "elevate-video-conference-experience-with-bigbluebutton-webrtc" && (
                      <StaticImage
                        src={"../../images/blog-9.png"}
                        alt="video conference with bigblue button webrtc"
                      />
                    )}
                    {node?.slug === "web3-in-ecommerce" && (
                      <StaticImage
                        src={"../../images/blog-10.png"}
                        alt="Web3 in E-Commerce: Paving the Way for a Retail Revolution"
                      />
                    )}
                    {node?.slug ===
                      "tokenization-in-revolutionizing-blockchain" && (
                      <StaticImage
                        src={"../../images/blog-11.png"}
                        alt="tokenization-in-revolutionizing-blockchain"
                      />
                    )}
                    {node?.slug === "consensus-mechanisms" && (
                      <StaticImage
                        src={"../../images/blog-12.png"}
                        alt="consensus-mechanisms"
                      />
                    )} */}
                     <img
                       src={`https://justtrytech.com/${blog?.node?.blogImg}`}
                      alt="Unraveling Defi explained in image"
                      className="w-100"
                    />
                  </Link>
                  <Card.Body>
                    <div className="developer-inner">
                      <div className="d-flex justify-content-between align-items-center">
                        <Link
                          to={`${blog?.node.slug}`}
                          className="text-decoration-none text-muted"
                        >
                          <h2
                            style={{
                              fontSize: "20px",
                              fontWeight: "600",
                              overflow: "hidden",
                              textOverflow: "ellipsis",
                              display: "-webkit-box",
                              WebkitLineClamp: 2,
                              WebkitBoxOrient: "vertical",
                              fontFamily: "Poppins",
                            }}
                            className="mb-4 text-dark fs-semibold pt-2 lh-sm"
                          >
                            {blog?.node?.title}
                          </h2>
                        </Link>
                      </div>
                      <p
                        style={{
                          overflow: "hidden",
                          textOverflow: "ellipsis",
                          display: "-webkit-box",
                          WebkitLineClamp: 2,
                          WebkitBoxOrient: "vertical",
                          fontSize: "14px",
                          lineHeight: "22px",
                        }}
                        className="cta-text mb-2 pt-2"
                      >
                        {blog?.node?.short_description}
                      </p>
                      <Link className="cta-text" to={`${blog?.node.slug}`}>
                        <p
                          style={{
                            fontSize: "14px",
                            lineHeight: "22px",
                            fontWeight: "600",
                            color: "#7534fc",
                          }}
                        >
                          Read More
                        </p>
                      </Link>
                      <div className="d-flex align-items-center justify-content-between pt-3">
                        <p
                          className="fs-4"
                          style={{ fontSize: "14px", lineHeight: "22px" }}
                        >
                          {blog?.node?.blog_created_date}
                        </p>
                        <p style={{ fontSize: "14px", lineHeight: "22px" }}>
                          Site Admin
                        </p>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default BlogsData;
